import { html } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import { MarketRowTemplate, TMarketRowTemplateArgs } from '../../market-row/storybook/market-row.templates';

export type TMarketActionCardTemplateArgs = Partial<HTMLMarketActionCardElement> & {
  // demo
  content?: string;
  rowArgs?: TMarketRowTemplateArgs;
};

export const MarketActionCardTemplate = ({
  // demo
  content,
  rowArgs,
  // props
  selected,
  disabled,
  transient,
  value,
}: TMarketActionCardTemplateArgs) => html`
  <market-action-card ?selected=${selected} ?disabled=${disabled} ?transient=${transient} value=${ifDefined(value)}>
    ${rowArgs ? MarketRowTemplate({ label: content, ...rowArgs }) : content}
  </market-action-card>
`;
